/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

/* Body styling */
body {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: linear-gradient(120deg, #f6f9fc, #dde8f4);
}

/* Container styling */
.login-container {
  background: #ffffff;
  padding: 40px;
  border-radius: 16px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
  transition: all 0.3s ease;
}

/* Header */
.login-container h2 {
  text-align: center;
  font-size: 28px;
  font-weight: 600;
  color: #1a202c;
  margin-bottom: 30px;
}

/* Input fields */
.input-group {
  position: relative;
  margin-bottom: 24px;
}

.input-group input {
  width: 100%;
  padding: 14px 16px;
  border: 1px solid #ccc;
  border-radius: 8px;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
  font-size: 16px;
  background-color: #f9fafb;
}

.input-group input:focus {
  border-color: #3182ce;
  box-shadow: 0 0 0 3px rgba(66, 153, 225, 0.4);
  outline: none;
}

.input-group label {
  display: block;
  margin-bottom: 8px;
  color: #4a5568;
  font-weight: 500;
}

/* Submit Button */
button[type="submit"] {
  width: 100%;
  padding: 14px;
  background-color: #2b6cb0;
  color: white;
  font-size: 16px;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  transition: background-color 0.3s ease;
  cursor: pointer;
}

button[type="submit"]:hover {
  background-color: #2c5282;
}

/* Error Message */
.error-message {
  margin-top: 12px;
  color: #e53e3e;
  text-align: center;
  font-size: 14px;
}
